@media screen and ( max-width: 767px ) {

    .nav_box {
        .soc {
            display: none; } }

    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0,0,0, .8);
        transition: 0.6s all;
        &_active {
            left: 0; }

        &_item {
            padding-right: 0;
            border: none;
            margin: 10px 0;
            a {
                color: #ffffff; } }

        &_link {
            font-style: 20px; } }
    nav {
        .hamburger {
            display: block;
            position: absolute;
            right: 10px;
            top: 25px;
            transform: translateY(-50%);
            width: 25px;
            height: 12px;
            span {
                display: block;
                height: 2px;
                width: 100%;
                background-color: #fff;
                margin-bottom: 2px;
                transition: 0.5s all; }
            &_active {
                span {
                    margin-bottom: -2px;
                    &:nth-child(1) {
                        transform: translateY(3px) rotate(-45deg); }
                    &:nth-child(2) {
                        display: none; }
                    &:nth-child(3) {
                        transform: translateY(3px) rotate(45deg); } } } } }

    .subheader {
        &_logo {
            img {
                padding-top: 3px;
                width: 100%; } }
        &_btn {
            width: 142px;
            height: 27px;
            font-size: 14px;
            margin-top: 13px;
            margin-left: 236px; }
        &_phon {
            display: block;
            margin-top: 10px;
            line-height: 0; } }

    .benefits {
        &_question {
            font-size: 18px;
            padding: 0;
            max-width: 240px; }
        &_text {
            font-size: 16px; } }

    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block;
        text-align: right; }

    .footer {} }

